.btn {
	display: inline-block;
	vertical-align: top;
	border-radius: 7px;
	font-family: Arial, sans-serif;
	font-weight: 700;
	text-align: center;
	text-decoration: none !important;
	&_small {
		padding: 15px 21px 16px;
		font-size: 12px;
		line-height: 14px;
		letter-spacing: 0.16px;
		@include sm {
			padding: 11px 21px;
			font-family: ArialMT, Arial, sans-serif;
			font-size: 17px;
			font-weight: 400;
			line-height: 19px; } }
	&_medium {
		padding: 18px 21px 19px;
		width: 225px;
		font-size: 20px;
		line-height: 22px;
		letter-spacing: 0.341177px;
		@include sm {
			padding: 10px 20px 12px;
			width: 162px;
			font-size: 17px;
			font-weight: 400;
			line-height: 19px;
			letter-spacing: 0.29px; } }
	&_large {
		padding: 22px 41px;
		font-size: 30px;
		line-height: 33px;
		letter-spacing: 0.511765px;
		@include sm {
			padding: 18px 20px 19px;
			font-size: 20px;
			line-height: 22px;
			letter-spacing: 0.341177px; } }
	&_white {
		color: #429dc9;
		background-color: $white;
		background-image: linear-gradient(180deg, rgba(243, 243, 243, 0.00113225) 6.45%, rgba(53, 54, 65, 0.300442) 97.89%);
		@include hover {
			color: $white;
			background-color: #F57827;
			background-image: linear-gradient(180deg, rgba(245, 120, 39, 0.36297) 7.32%, #D85805 92.46%); } }
	&_orange {
		color: $white;
		background-color: #F57827;
		background-image: linear-gradient(180deg, rgba(245, 120, 39, 0.36297) 7.32%, #D85805 92.46%);
		@include hover {
			color: #429dc9;
			background-color: $white;
			background-image: linear-gradient(180deg, rgba(243, 243, 243, 0.00113225) 6.45%, rgba(53, 54, 65, 0.300442) 97.89%); } }
	&_blue {
		color: $white;
		background-color: #429dc9;
		background-image: linear-gradient(180deg, rgba(58, 153, 200, 0.386521) 33.9%, rgba(53, 54, 65, 0.480214) 100%);
		@include hover {
			color: #429dc9;
			background-color: $white;
			background-image: linear-gradient(180deg, rgba(243, 243, 243, 0.00113225) 6.45%, rgba(53, 54, 65, 0.300442) 97.89%); } }
	&_wide {
		width: 100%; } }
