.form {
	padding: 81px 20px 123px;
	background: url(../img/form-bg-1.svg) repeat center center #069ecd;
	@include sm {
		padding: 60px 0 93px; }
	&_single {
		padding-top: 93px;
		padding-bottom: 209px;
		@include sm {
			padding-top: 37px;
			padding-bottom: 79px; }
		.form {
			&__container {
				max-width: 537px;
				@include xs {
					max-width: none; } }
			&__body {
				padding: 51px 80px 73px;
				@include xs {
					padding: 43px 33px 70px; } } } }
	&_multiple {
		.form {
			&__container {
				max-width: 901px;
				@include sm {
					border-radius: 0; } }
			&__body {
				padding: 55px 52px 72px;
				@include xs {
					padding: 38px 33px 58px; } }
			&__footer {
				margin-top: 60px;
				@include sm {
					margin-top: 42px; } }
			&__btn {
				width: 50%;
				@include sm {
					width: 100%; } } } }
	&__container {
		margin: 0 auto;
		border-radius: 10px;
		background: rgba(53, 54, 65, 0.27123);
		@include xs {
			border-radius: 0; } }
	&__title {
		padding: 22px 20px 28px;
		border-radius: 10px;
		color: $white;
		background: #343741;
		font-family: ArialMT, Arial, sans-serif;
		font-size: 30px;
		font-weight: 700;
		line-height: 33px;
		text-align: center;
		text-transform: uppercase;
		@include sm {
			padding: 28px 20px;
			border-radius: 0;
			font-size: 23px;
			line-height: 26px; } }
	&__field {
		&:not(:last-child) {
			margin-bottom: 20px; } }
	&__fields {
		display: flex;
		flex-wrap: wrap;
		margin: -10px -20px;
		.form__field {
			flex-grow: 1;
			padding: 10px 20px;
			margin: 0 !important;
			width: 100%;
			&_half {
				width: 50%;
				@include sm {
					width: 100%; } } }
		.form__fields {
			margin: -10px -9px;
			.form__field {
				padding: 10px 9px;
				&_half {
					@include sm {
						width: 50%; }
					@include xs {
						width: 100%; } } } } }
	&__label {
		display: block;
		margin-bottom: 12px;
		color: $white;
		font-family: Verdana;
		font-size: 18px;
		line-height: 22px; }
	&__footer {
		margin-top: 42px;
		text-align: center; }
	&__btn {
		@include sm {
			max-width: 226px; } } }

.hero-triangle {
	position: relative;
	overflow: hidden;
	display: none;
	// margin: -37px -0px 37px
	margin: -37px -0px 37px;
	@include sm {
		display: block; }
	&:before {
		content: '';
		position: relative;
		left: 50%;
		display: block;
		@include arr(900, 95, #151824, b);
		transform: translateX(-50%); } }
