body {
	min-width: 320px;
	background: $white;
	font-family: Arial, sans-serif;
	line-height: 1.25; }

.title-lg {
	font-family: ArialMT, Arial, sans-serif;
	font-weight: 400;
	font-size: 42px;
	line-height: 44px;
	@include sm {
		font-size: 30px;
		line-height: 33px;
		font-weight: 700; } }

.title-md {
	font-family: ArialMT, Arial, sans-serif;
	font-size: 36px;
	font-weight: 400;
	line-height: 64px;
	text-align: center;
	@include sm {
		font-family: Verdana;
		font-size: 16px;
		line-height: 19px; } }

.visible-desktop {
	@include sm {
		display: none; } }

.visible-mobile {
	@include from('sm') {
		display: none; } }
